import React from 'react';
import Footer from './Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import CurrencyInput from 'react-currency-input-field';

export default function EmergencyFund(props) {

	const [emergencyFund, setEmergencyFund] = React.useState("");

	const [loading, setLoading] = React.useState(true);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state

	const chipset = [
		{ "key": "0", "name": "No emergency fund" },
		{ "key": "50000", "name": "₹ 50,000" },
		{ "key": "200000", "name": "₹ 2 Lakh" },
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setEmergencyFund(p.emergencyFund || "")
			} else {
				setEmergencyFund("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (emergencyFund) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [emergencyFund])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={36} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="bodyTextDark">What is the size of your emergency fund?</div>
							<CurrencyInput
									className='normalInput rupee'
									id="emergencyFund"
									name="emergencyFund"
									placeholder="1,00,000"
									value={emergencyFund}
									allowDecimals={false}
									allowNegativeValue={false}
									disableAbbreviations={false}
									prefix=' '
									onValueChange={(value) => setEmergencyFund(value)}
									autoFocus
									intlConfig={{ locale: 'en-IN', currency: 'INR' }}
								/>
							<div className='hiddden-scrollbar'>
								{chipset.map((item) => {
									return (
										<Chip
											className={emergencyFund===item.key?"normalChip selected":"normalChip"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setEmergencyFund(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/risk-profile/5"
				state={{...STATE,emergencyFund}}
				saveData={{ emergencyFund }}
				buttonText="Confirm"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
